import React, { useEffect, useState } from 'react'
import './style.css'
import * as share from '../../../assets/images/Levels_Screen/share icon.svg'
import * as settings from '../../../assets/images/Levels_Screen/settings icon.svg'
import * as save from '../../../assets/images/Levels_Screen/save icon.svg'
import * as instagram from '../../../assets/Clean Walk/instagram.png'
import * as Audio_icon_ON from '../../../assets/Clean Walk/Audio_icon_ON.png'
import * as Audio_icon from '../../../assets/Clean Walk/Audio_icon.png'
import * as Vibration_Icon_ON from '../../../assets/Clean Walk/Vibration Icon_ON.png'
import * as Vibration_Icon from '../../../assets/Clean Walk/Vibration Icon.png'
import * as Clear_Data from '../../../assets/Clean Walk/clear.png'

import * as google from '../../../assets/Clean Walk/google.svg'
import * as facebook from '../../../assets/Clean Walk/facebook.svg'
import { GoogleLogin } from 'react-google-login';
import FacebookProvider, { Login } from 'react-facebook-sdk';

import { Sociallogin } from '../../../utils/actions'

import {
  FacebookShareButton,
  FacebookIcon,
  WhatsappIcon,
  WhatsappShareButton,
  TwitterShareButton,
  TwitterIcon,
} from "react-share";

import * as backimgtop from '../../../assets/Clean Walk/Levels_Screen/BG_top.svg';
import * as logo from '../../../assets/Clean Walk/Loading_Screen/Search.logo.small.jpg.png'
import { useHistory } from 'react-router-dom';
import { loader, SyncData } from '../core'



const useAudio = url => {
  const [audio] = useState(new Audio(url));
  const [playing, setPlaying] = useState(false);

  const toggle = () => setPlaying(!playing);

  useEffect(() => {
    playing ? audio.play() : audio.pause();
  },
    [playing]
  );

  useEffect(() => {
    audio.addEventListener('ended', () => setPlaying(false));
    return () => {
      audio.removeEventListener('ended', () => setPlaying(false));
    };
  }, []);

  return [playing, toggle];
};


const HeaderComponent = () => {
  const [playing, toggle] = useAudio("https://www.learningcontainer.com/wp-content/uploads/2020/02/Kalimba.mp3");
  const [isshare, setisshare] = useState(false)
  const [vib, setvib] = useState(false)
  const [isSettings, setissettings] = useState(false)
  const history = useHistory();
  const [loginbutton, setloginbutton] = useState(false)
  const [otpsent, setotpsent] = useState(false)
  const [isSocialdone, setisSocialdone] = useState(true)

  useEffect(() => {
    let listItems = String(localStorage.getItem('UsedSocialLoginMethod'));
    if (listItems === "true")
      setisSocialdone(false)
  }, [])


  const loginCallback = async (data: any, errorresponse: any) => {
    if (data.status === 200) {
      console.log('response ' + JSON.stringify(data));
      localStorage.setItem('AuthToken', JSON.stringify(data.data.result.token));
      localStorage.setItem('UserDetails', JSON.stringify(data.data.result.user_details));
      localStorage.setItem('UsedSocialLoginMethod', "true");
      setisSocialdone(false)
    } else {
      console.log('error ' + JSON.stringify(data));
      console.log('error ' + JSON.stringify(errorresponse));
    };
  }

  return (
    <div className='tasktopcontainer'>
      <div className='tasklogoposition' >
        <img onClick={() => {
          history.replace('/TaskListScreen/')
          setisshare(false)
          setloginbutton(false)
          setissettings(false)
        }} className='tasklogoicon' src={logo} />
      </div>
      <>
        {isshare ?
          <div className='share_popupfullscreen'>
            <img className='bgimgtop' src={backimgtop} />
            <div className='menu_mainwindow'>

              <div className='titletext'>SHARE</div>


              <div className="share_fb">

                <div className='fb1'>
                  <WhatsappShareButton
                    url={"https://thesearch.bengalurusustainabilityforum.org"}>
                    <WhatsappIcon size={'16vw'} round={true} />
                  </WhatsappShareButton>
                </div>

                <div className='fb2main'>
                  <div className='fb2'>
                    <FacebookShareButton className='fbicon'
                      url={"https://thesearch.bengalurusustainabilityforum.org"}>
                      <FacebookIcon size={'16vw'} round={true} />
                    </FacebookShareButton>
                  </div>
                  <div className='fb2'>
                    <TwitterShareButton className='fbicon'
                      url={"https://thesearch.bengalurusustainabilityforum.org"}>
                      <TwitterIcon size={'16vw'} round={true} />
                    </TwitterShareButton>
                  </div>
                </div>

                <div className='fb3'>
                  <img onClick={() => {
                    if (window.confirm('The text has been copied to clipboard whould you like to continue with instagram')) {
                      window.location.href = 'https://www.instagram.com/?hl=en';
                      navigator.clipboard.writeText("https://thesearch.bengalurusustainabilityforum.org")
                    };
                  }}
                    className='share_instaicon' src={instagram} />
                </div>

              </div>

              <div className="share_container">
                <div className='share_TextTitle'>Enjoyed the game?</div>
                <div className='share_TextTitle'>Let others know!</div>
              </div>

            </div>
            <div className="bgbottombluebar" />
          </div>
          :
          null
        }
        {isSettings ?
          <div className='share_popupfullscreen'>
            <img className='bgimgtop' src={backimgtop} />
            <div className='menu_mainwindow'>

              <div className='titletext'>SETTINGS</div>

              <div className="settings_listitem_container">

                <div onClick={() => {
                  setvib(!vib)
                  window.navigator.vibrate([100, 30, 100, 30, 100, 30, 200,
                    30, 200, 30, 200, 30, 100, 30, 100, 30, 100])
                }}
                  className="settings_listitem">
                  <div className='settings_list_starting_semicircle' />
                  <div className="settings_listtext">PHONE VIBRATION</div>
                  {vib ? <img className='settings_buttons' src={Vibration_Icon_ON} /> : <img className='settings_buttons' src={Vibration_Icon} />}

                </div>


                <div onClick={toggle} className="settings_listitem">
                  <div className='settings_list_starting_semicircle' />
                  <div className="settings_listtext">BUTTON AUDIO</div>
                  {playing ? <img className='settings_buttons' src={Audio_icon_ON} /> : <img className='settings_buttons' src={Audio_icon} />}
                </div>

                <div onClick={() => {
                  if (window.confirm('would you like to reset the game?')) {
                    loader()
                  };
                }} className="settings_listitem">
                  <div className='settings_list_starting_semicircle' />
                  <div className="settings_listtext">Reset Data</div>
                  <img className='settings_buttons' src={Clear_Data} />
                </div>

              </div>

            </div>
            <div className="bgbottombluebar" />
          </div>
          :
          null
        }
        {
          loginbutton ?
            <>{isSocialdone ?
              <div className='login_popupfullscreen'>
                <img className='bgimgtop' src={backimgtop} />
                <div className='menu_mainwindow'>

                  <div className='titletext'>LOGIN</div>
                  <div className='loginDescription_Text'>Please select your preferred method to save your game progress.</div>
                  <div className='login_popupformcontainer'>


                    <div className="login_button_container">

                      <GoogleLogin
                        clientId="581422038025-rte3a06d7kumasu887n64uikerfigmiv.apps.googleusercontent.com"
                        render={renderProps => (
                          <button className="login_googlebutton" onClick={renderProps.onClick} disabled={renderProps.disabled}>
                            <img src={google} className="login_googleicon" />
                            <div className="login_buttontext">Continue with Google</div>
                          </button>
                        )}
                        buttonText="Login"
                        onSuccess={Response => {
                          console.log(Response)
                          var formData = new FormData()
                          let userInfo = Response.profileObj
                          formData.append('lastname', userInfo.familyName);
                          formData.append('firstname', userInfo.givenName);
                          formData.append('photo_url', userInfo.imageUrl);
                          formData.append('auth_provider', "google");
                          formData.append('email', userInfo.email);
                          let userdetails = JSON.parse(String(localStorage.getItem("UserDetails")))
                          console.log("=========>", userdetails.user);
                          formData.append('username', userdetails.user);
                          Sociallogin(loginCallback, formData)
                        }}
                        onFailure={(Response: any) => { console.log(Response) }}
                        cookiePolicy={'single_host_origin'}
                      />

                    </div>


                    <div className="login_button_container">

                      <FacebookProvider appId="976961256166749">
                        <Login
                          scope="email"
                          onResponse={(response: any) => {
                            console.log(response.profile)
                            //  email: "medi.cann6@gmail.com"
                            // first_name: "Medi"
                            // id: "213891626989372"
                            // last_name: "Cann"
                            // name: "Medi Cann"
                            console.log(Response)
                            var formData = new FormData()
                            let userInfo = response.profile
                            formData.append('lastname', userInfo.first_name);
                            formData.append('firstname', userInfo.last_name);
                            formData.append('photo_url', "");
                            formData.append('auth_provider', "fb");
                            formData.append('email', userInfo.email);
                            let userdetails = JSON.parse(String(localStorage.getItem("UserDetails")))
                            console.log("=========>", userdetails.user);
                            formData.append('username', userdetails.user);
                            Sociallogin(loginCallback, formData)
                          }}
                          onError={(response: any) => {
                            console.log(response)
                          }}
                        >
                          <button className="login_facebookbutton">
                            <img src={facebook} className="login_facebookicon" />
                            <div className="login_buttontext">Continue with Facebook</div>
                          </button>
                        </Login>
                      </FacebookProvider>
                    </div>
                    {otpsent ? <>
                      <div className="login_button_container">
                        <input type="text" className="login_otpinput" />
                      </div>

                      <div className="login_button_container">
                        <button onClick={() => { }} className="login_validatebutton">
                          <div className="login_buttontext">Validate</div>
                        </button>
                      </div>
                    </>

                      :
                      <>
                        <div className="login_button_container">
                          <input type="email" className="login_otpinput" placeholder="Enter Your Email address" />
                        </div>

                        <div className="login_button_container">
                          <button onClick={() => { setotpsent(true) }} className="login_validatebutton">
                            <div className="login_buttontext">Get OTP</div>
                          </button>
                        </div>
                      </>

                    }
                  </div>
                </div>
                <div className="bgbottombluebar" />
              </div> :
              <div className='login_popupfullscreen'>
                <img className='bgimgtop' src={backimgtop} />
                <div className='menu_mainwindow'>

                  <div className='titletext'>Welcome back</div>
                  <div className='loginDescription_Text'>would you like to save progress?</div>
                  <div className='login_popupformcontainer'>

                    <div className="login_button_container">
                      <button onClick={() => {
                        SyncData()
                      }} className="login_validatebutton">
                        <div className="login_buttontext">yes</div>
                      </button>
                    </div>
                    <div className="login_button_container">
                      <button onClick={() => {
                        setloginbutton(!loginbutton)
                      }} className="login_validatebutton">
                        <div className="login_buttontext">go back</div>
                      </button>
                    </div>
                  </div>
                </div>
                <div className="bgbottombluebar" />
              </div>
            }</> : null
        }

        <div className='share_icons'>

          <div className='share_iconset' onClick={() => {
            setisshare(!isshare)
            setloginbutton(false)
            setissettings(false)

          }}>
            <div className='share_setimg'>

              <img className='share_icon' src={share} />
            </div>
            <div className='share_iconText'>SHARE</div>
          </div>

          <div className='share_iconset' onClick={() => {
            setisshare(false)
            setloginbutton(!loginbutton)
            setissettings(false)
          }}>
            <div className='share_setimg'>

              <img className='share_icon' src={save} />
            </div>
            <div className='share_iconText'>SAVE</div>
          </div>

          <div className='share_iconset' onClick={() => {
            setisshare(false)
            setloginbutton(false)
            setissettings(!isSettings)
          }}>

            <div className='share_setimg'>
              <img className='share_icon' src={settings} />
            </div>

            <div className='share_iconText'>SETTINGS</div>
          </div>
        </div>
      </>



    </div>
  )
}

export default HeaderComponent


